body {
  background-image: url("wallpaper.png");
  margin: 0;
  font-family: 'Luminari', fantasy;
  font-size: large;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.error {
  color: maroon;
}

.nugget {
  border-radius: 10px;
  opacity: 90%;
  background-color: white;
  padding: 2%;
}
#candy-canes {
  height: 75px;
}
#root {
  width:100%;
  padding: 2em;
  display: flex;
  justify-content: center;
}

#candy-wrapper {
  width: 100%;
  margin: 0;
  padding: 10px 0px;
  display: flex;
  justify-content: center;
}
